import React from 'react';
import {useHistory} from "react-router";
import { Link } from "react-router-dom";
import Profile from "../user/Profile";
import Logo from "../../image/Logo.png";
import "../../css/Top.css";
import "../../css/font.css";

export default function Top(){
    const history = useHistory();
    const access_token = localStorage.getItem('access_token');

    function start() {
        try {
            if(access_token !== null) {
                history.push("/select");
            } else {
                alert('   .');
            }
        } catch (err) {
            console.error(err);
        }
    }

    function moveToComm() {
        try {
            if(access_token !== null) {
                history.push("/community");
            } else {
                alert('   .');
            }
        } catch (err) {
            console.error(err);
        }
    }

    function moveToQna() {
        try {
            if(access_token !== null) {
                history.push("/qna");
            } else {
                alert('   .');
            }
        } catch (err) {
            console.error(err);
        }
    }

    return (
        <div className='top'>
            <div className='topMenu'>
                <Link to='/'>
                    <img src={Logo} className='img_logo' alt="PictoMaker-Logo" style={{width:"100px",height:"50px"}}/>
                </Link>
                <div className='GnbMenu'>
                    <Link to ='/introduce'>
                        <div></div>
                    </Link>
                    <div onClick={start} style={{cursor:"pointer"}}></div>
                    <div onClick={moveToQna}></div>
                    <div onClick={moveToComm}></div>
                    {access_token === null ? <div></div> : <Profile />}
                </div>
            </div>
        </div>
    );
}