import React, {useEffect} from 'react';
import "aos/dist/aos.css";
import AOS from "aos";
import "../../css/Introduce.css"
import "../../App.css"
import Logo from "../../image/Logo.png"
import Step1 from "../../image/step1.svg"
import Step2 from "../../image/step2.svg"
import Step3 from "../../image/step3.svg"
import { motion } from "framer-motion";




export default function Introduce(){
    useEffect(() => {
        AOS.init({
            duration : 1000
        });
    });

    return(
        <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      transition={{ ease: "easeOut", duration: 1 }}
    >
        <div className='Introduce'>
            <div class="col-md-7 intro-content intro-content1" data-aos="fade-right">
                <div className='intro-title'>
                    <div> </div>
                    <div className='br-title'>   .</div>
                </div>
                <div className='intro-subtitle'>
                    <div className='intro-qeustion'><img src={Logo} alt={""} style={{width:"70px",height:"35px"}}/>?</div>
                        <div className='circle circle1'></div>
                        <div className='circle circle2' ></div>
                        <div className='circle circle3'></div>
                        <div className='circle circle4'></div>
                    <div className='intro-answer'>
                        <p className='answer-title'>Pictogram + Maker</p>
                        <p className='answer-subtit'>   <br/>   (Pictogram)  <br/> .</p>
                    </div>
                </div>
            </div>

            <div class="col-md-7 intro-content intro-content2" data-aos="fade-left">
               <div className='intro-title'>
                   <div>  </div>
                   <div  className='br-title'>3   !</div>
               </div>
               <div className='intro-manual'>
                   <div className='step step1'>
                        <img src={Step1} alt="1"/>
                   </div>
                   <div className='step step2'>
                        <img src={Step2} alt="2"/>
                   </div>
                   <div className='step step3'>
                        <img src={Step3} alt="3"/>
                   </div>
               </div>
               <div className='intro-manual'>
                    <div className='step step1-1'>
                        <div className='step-title step1-title'>
                            <div className='title-square'><p>Step1</p></div>
                            <div>    !</div>
                        </div>
                        <div className='step-desc'>
                            ,   <br/> !<br/>
                               <br/>   .
                        </div>
                    </div>
                    <div className='step step2-1'>
                        <div className='step-title step2-title'>
                            <div className='title-square'><p>Step2</p></div>
                            <div>   !</div>
                        </div>
                        <div className='step-desc'>
                              <br/>  !<br/>
                               !
                        </div>
                    </div>
                    <div className='step step3-1'>
                        <div className='step-title step3-title'>
                            <div className='title-square'><p>Step3</p></div>
                            <div>  !</div>
                        </div>
                        <div className='step-desc'>
                              <br/> !<br/>
                             PNG   .
                        </div>
                    </div>
               </div>

            </div>

            <div class="col-md-7 intro-content intro-content3" data-aos="fade-right">
               {/*fade  */}
            </div>


            <div class="col-md-7 intro-content intro-content4" data-aos="fade-left">
                {/*fade  */}
            </div>
        </div>
        </motion.div>
    );
}